  .dct_tm_preloader.loaded {
    opacity: 0;
    visibility: hidden; }
  .dct_tm_preloader:before, .dct_tm_preloader:after {
    content: "";
    position: absolute;
    height: 50%;
    width: 100%;
    background-color: #041230;
    -webkit-transition: .7s all ease;
    -o-transition: .7s all ease;
    transition: .7s all ease; }
  .dct_tm_preloader:before {
