}

h6 {
  font-size: 16px;
}

table {
  width: 100%;
  max-width: 100%;
  text-align: left;
  border-collapse: collapse;
  border-spacing: 0px;
  margin-bottom: 15px;
  color: #999;
  border: 1px solid #eee;
  tr {
    border: 1px solid #eee;
  }
  th {
    vertical-align: top;
    color: #333;
    background: none;
    font-size: 16px;
    font-weight: normal;
    padding: 15px 10px;
    border: 1px solid #eee;
  }
  td {
    vertical-align: top;
    padding: 10px;
    border: 1px solid #eee;
  }
}

code {
  padding: 10px;
  padding-bottom: 0;
  border-left: 3px solid #6FBF71;
  border-bottom: 1px solid transparent;
  background: #f7f7f7;
  overflow-x: scroll;
}

pre {
  padding: 10px;
  padding-bottom: 0;
  border-left: 3px solid #6FBF71;
  border-bottom: 1px solid transparent;
  background: #f7f7f7;
  overflow-x: scroll;
  display: block;
  word-break: break-all;
  word-wrap: break-word;
