/*------------------------------------------------------------------*/
/*	02) FORM ELEMENTS
/*------------------------------------------------------------------*/

textarea {
  border-radius: 0;
  font-size: 13px;
  color: #333;
  resize: vertical;
  font-family: "Open Sans";
  letter-spacing: 1.2px !important;
  -webkit-box-sizing: border-box;
  /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;
  /* Firefox, other Gecko */
  -ms-box-sizing: border-box;
  /* IE 10 + */
  box-sizing: border-box;
  /* Opera/IE 8+ */
  line-height: 1.4;
  padding: 10px !important;
  width: 100%;
  border: 1px solid #eee;
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -ms-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}

input {
  &[type="text"], &[type="password"], &[type="datetime"], &[type="datetime-local"], &[type="date"], &[type="month"], &[type="time"], &[type="week"], &[type="number"], &[type="email"], &[type="url"], &[type="search"], &[type="tel"], &[type="color"] {
    border-radius: 0;
    font-size: 13px;
    min-width: auto;
    color: #333;
    letter-spacing: 1.4px;
    -webkit-box-sizing: border-box;
    /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;
    /* Firefox, other Gecko */
    -ms-box-sizing: border-box;
    /* IE 10 + */
    box-sizing: border-box;
    /* Opera/IE 8+ */
    font-family: "Open Sans";
    line-height: 1.6;
    padding: 10px !important;
    height: 44px;
    vertical-align: middle;
    border: 1px solid #eee;
    -webkit-transition: all .3s ease;
    -moz-transition: all .3s ease;
    -ms-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
  }
}

.uneditable-input {
  border-radius: 0;
  font-size: 13px;
  min-width: auto;
  color: #333;
  letter-spacing: 1.4px;
  -webkit-box-sizing: border-box;
  /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;
  /* Firefox, other Gecko */
  -ms-box-sizing: border-box;
  /* IE 10 + */
  box-sizing: border-box;
  /* Opera/IE 8+ */
  font-family: "Open Sans";
  line-height: 1.6;
  padding: 10px !important;
  height: 44px;
  vertical-align: middle;
  border: 1px solid #eee;
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -ms-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}

textarea:focus {
  border-color: #ccc;
  background: #eee;
}

input {
  &[type="text"]:focus, &[type="password"]:focus, &[type="datetime"]:focus, &[type="datetime-local"]:focus, &[type="date"]:focus, &[type="month"]:focus, &[type="time"]:focus, &[type="week"]:focus, &[type="number"]:focus, &[type="email"]:focus, &[type="url"]:focus, &[type="search"]:focus, &[type="tel"]:focus, &[type="color"]:focus {
    border-color: #ccc;
    background: #eee;
  }
}

.uneditable-input:focus {
  border-color: #ccc;
  background: #eee;
}

input[type="submit"] {
  padding: 13px 15px;
  height: 40px;
  line-height: 1;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 600;
  text-decoration: none;
  letter-spacing: 0.5px;
  border: none;
  cursor: pointer;
  color: #FFF;
  background: #777;
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -ms-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
  &:hover {
    background: #444;
  }
}

select {
  padding: 8px;
  border: 2px solid #eee;
}

input[type="button"] {
  cursor: pointer;
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -ms-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}

