@font-face {
  font-family: 'xcon';
  src: url('../assets/font/xcon.eot?4965248');
  src: url('../assets/font/xcon.eot?4965248#iefix') format("embedded-opentype"), url('../assets/font/xcon.woff2?4965248') format("woff2"), url('../assets/font/xcon.woff?4965248') format("woff"), url('../assets/font/xcon.ttf?4965248') format("truetype"), url('../assets/font/xcon.svg?4965248#xcon') format("svg");
  font-weight: normal;
  font-style: normal;
}


/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
/*
@media screen and (-webkit-min-device-pixel-ratio:0) {
  @font-face {
    font-family: 'xcon';
    src: url('../../assets/font/xcon.svg?4965248#xcon') format('svg');
  }
}
*/

[class^="xcon-"]:before, [class*=" xcon-"]:before {
  font-family: "xcon";
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */
  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;
  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;
  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */
  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}

